import * as React from "react";
import { FunctionComponent, useMemo, type CSSProperties } from "react";
import * as styles from "./flight-card-container.module.css";

type FlightCardContainerType = {
  flightDetailsAirlineDetai?: string;
  airlineName?: string;
  flightTimeDepartureTime?: string;
  dimensionCode?: string;
  flightDurationLayoverDura?: string;
  timeLabel?: string;
  price?: string;

  /** Style props */
  propTransform?: CSSProperties["transform"];
  propWidth?: CSSProperties["width"];
};

const FlightCardContainer: FunctionComponent<FlightCardContainerType> = ({
  flightDetailsAirlineDetai,
  airlineName,
  flightTimeDepartureTime,
  dimensionCode,
  flightDurationLayoverDura,
  timeLabel,
  price,
  propTransform,
  propWidth,
}) => {
  const turkishIconStyle: CSSProperties = useMemo(() => {
    return {
      transform: propTransform,
    };
  }, [propTransform]);

  const flightDurationStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  return (
    <div className={styles.flightCard}>
      <div className={styles.airlineSection}>
        <img
          className={styles.turkishIcon}
          alt=""
          src={flightDetailsAirlineDetai}
          style={turkishIconStyle}
        />
        <div className={styles.singaporeAirlines}>{airlineName}</div>
      </div>
      <div className={styles.flightDetailsSection}>
        <div className={styles.departureDetails}>
          <div className={styles.am}>{flightTimeDepartureTime}</div>
          <div className={styles.sin}>SIN</div>
        </div>
        <div className={styles.flightduration} style={flightDurationStyle}>
          <img className={styles.flighticon} alt="" src={dimensionCode} />
          <div className={styles.h55mNonStop}>{flightDurationLayoverDura}</div>
        </div>
        <div className={styles.arrivalDetails}>
          <div className={styles.am1}>{timeLabel}</div>
          <div className={styles.sin}>LAX</div>
        </div>
      </div>
      <div className={styles.priceSection}>
        <img className={styles.priceSectionChild} alt="" src="/vector-1.svg" />
        <b className={styles.s730}>{price}</b>
      </div>
    </div>
  );
};

export default FlightCardContainer;
